div.post {
  margin: 15px;
  border: none;
  box-shadow: tomato 5px 4px 20px;
  float: left;
  width: 180px;
}

div.post:hover {
  box-shadow: rgb(184, 30, 3) 5px 4px 10px;
}

div.gallery:hover {
  border: 1px solid #777;
}

div.post {
  width: 8cm;
  height: auto;
}

div.desc {
  padding: 10px;
  text-align: center;
}

.footer p {
  font-size: 30px;
  margin-top: 19cm;
  font-family: "Oleo Script", cursive;
  font-weight: 800;
}

[data-theme="dark"] {
  --bg-color: linear-gradient(blue, red);
  --text-color: #fff;
}

@media (min-width: 600px) {
  div {
    flex-basis: 33%;
  }
}
